<template>
  <div class="order">
    <div class="title">
      我的订单
    </div>
    <div class="search-form">
      <el-form label-width="80px" label-position="left">
        <el-form-item label="日期范围">
          <el-date-picker
            v-model="form.time"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="订单号" style="width:400px;">
          <el-input v-model="form.orderNum" />
        </el-form-item>
        <el-form-item label="产品名称" style="width:400px;">
          <el-input v-model="form.name" placeholder="商品精确搜索" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary">搜索订单</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-div">
      <div class="table">
        <div class="table-title">
          <span class="time">2020-07-02 10:54:31 </span>
          <span class="number">订单号：</span>
          <span class="number-value">2020070210543186802 </span>
          <span class="download t-link primary">下载数据</span>
        </div>
        <el-table border :data="tableData" style="width: 100%">
          <el-table-column prop="date" label="商品" align="center">
            <template slot-scope="{ row }">
              <div class="goods flex-center-y" @click="toDetail(row)">
                <el-image :src="row.url" style="width: 100px"></el-image>
                {{ row.title }}
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="color"
            label="颜色"
            align="center"
            width="80px"
          >
          </el-table-column>
          <el-table-column prop="size" label="尺寸" align="center" width="80px">
          </el-table-column>
          <el-table-column
            prop="price"
            label="单价（元）"
            align="center"
            width="100px"
          >
          </el-table-column>
          <el-table-column
            prop="count"
            label="数量"
            align="center"
            width="80px"
          >
          </el-table-column>
          <el-table-column
            prop="pay"
            label="实付款（元）"
            align="center"
            width="120px"
          >
          </el-table-column>
          <el-table-column prop="state" label="交易状态" align="center">
          </el-table-column>
          <el-table-column prop="remark" label="备注" align="center">
            <template slot-scope="{ row }">
              <div class="remark pointer" @click="changeRemark">
                {{ row.remark || "备注" }}
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Order',
  data () {
    return {
      form: {
        startTime: '',
        endTime: '',
        orderNum: '',
        name: ''
      },
      tableData: [
        {
          id: '7037',
          gid: '7037',
          title: '彪马情侣短T(1)',
          url: require('@/assets/images/200(1).jpg'),
          color: '黑色',
          size: '5XL',
          price: '29',
          count: 1,
          pay: 29,
          state: '已确认',
          remark: ''
        }
      ]
    }
  },
  methods: {
    toDetail (row) {
      this.$router.push({ path: '/details', query: { gid: row.gid } })
    },
    changeRemark (row) {
      console.log('备注')
    }
  }
}
</script>
<style lang="scss" scoped>
.order {
  .title {
    border-bottom: 3px solid $color_primary;
    padding: 12px 20px;
  }
  .search-form {
    padding-top: 20px;
    padding-left: 20px;
  }
  .table-div {
    padding: 20px;
    .table {
      .table-title {
        padding: 12px 0;
        .time,.number-value {
          margin-right: 20px;
        }
        .number-value{

        }
      }
      .goods {
        cursor: pointer;
        &:hover {
          color: $color_primary;
        }
      }
    }
  }
}
</style>
